/* modules */

.mfp-bg {
  opacity: 1;
  background-color: rgba($color-black, 0.4);
  position: fixed !important; }

/* popup */
.popup {
  position: relative;
  background: $color-white;
  padding: 26px;
  width: auto;
  max-width: 280px;
  margin: 20px auto;
  border-radius: 4px;
  &__title {
    text-transform: uppercase;
    color: $color-red;
    text-align: center;
    font-size: rem(22);
    font-family: $font-roboto-bold;
    line-height: 1;
    margin-bottom: 22px; }
  &--location {
    max-width: 750px;
    padding-left: 50px;
    padding-right: 50px; }
  &__close {
    cursor: pointer;
    position: absolute;
    right: 15px;
    top: -15px;
    width: 31px;
    height: 32px;
    background: $color-red;
    color: $color-white;
    transform: skewX(-20deg);
    border-radius: 3px;
    line-height: 32px;
    padding-left: 9px;
    font-size: rem(16);
    transition: background 0.35s;
    i {
      line-height: 1;
      display: inline-block;
      transform: skewX(20deg);
      margin-left: 2px; }
    &:hover {
      background: $color-lightred; } }
  &__info {
    color: $color-white;
    background: $color-red;
    font-size: rem(13);
    font-family: $font-roboto-bold;
    padding: 9px 10px;
    line-height: 1.2;
    text-align: center;
    &--location {
      margin: 0 -50px 25px -50px; } }
  &__search {
    margin-bottom: 25px;
    &-wrapper {
      display: table;
      width: 100%;
      &>div {
        display: table-cell;
        vertical-align: top;
        width: 100%;
        &:last-child {
          width: 1px; } }
      .input {
        transform: skewX(-15deg);
        border: 1px solid $color-lightgray;
        background: $color-lightgray;
        border-radius: 3px;
        position: relative;
        overflow: hidden;
        display: block;
        input {
          transform: skewX(15deg);
          background: none;
          height: 33px;
          border: 0;
          padding: 0 20px 0 20px;
          width: 100%;
          font-size: rem(12);
          font-family: $font-roboto; } }
      &.is-focus {
        .input {
          background: transparent;
          border-color: #e0e0e0;
          label {
            opacity: 0;
            visibility: hidden; } } } } }
  &__city {
    font-family: $font-roboto;
    ul {
      margin: 0;
      padding: 0;
      li {
        margin: 0;
        list-style: none;
        display: inline-block;
        width: 24%;
        &>a, &>span {
          display: inline-block;
          text-decoration: none;
          color: $primary-color;
          white-space: nowrap;
          line-height: 24px; }
        &>a {
          &:hover {
            color: $color-red; } } } } }
  &__form-bottom {
    background: $color-lightgray;
    text-align: center;
    font-size: rem(13);
    height: 72px;
    margin: 24px -26px -26px -26px;
    border-radius: 0 0 4px 4px;
    padding-top: 18px;
    strong {
      font-size: rem(14);
      text-transform: uppercase;
      // font-family: $font-roboto
      display: block;
      line-height: 1;
      margin-bottom: 5px; } }
  &__wrapper {
    .btn {
      margin-bottom: rem(13); } }

  &__social {
    &-head {
      display: table;
      width: 100%;
      margin-bottom: 12px;
      &>* {
        display: table-cell;
        vertical-align: middle; }
      span {
        padding: 0 5px;
        white-space: nowrap;
        color: $color-red;
        width: 1px; }
      &::after, &::before {
        display: table-cell;
        content: '';
        background-image: linear-gradient(#e5e5e5, #e5e5e5);
        background-size: 100% 1px;
        background-repeat: no-repeat;
        background-position: center;
 } }        // width: 100%
    &-body {
      text-align: center;
      p {
        font-size: rem(12);
        margin-bottom: 12px; }
      a {
        display: inline-block;
        margin: 0 4px;
        opacity: 1;
        &:nth-of-type(1) {
          margin-left: 0; }
        &:last-child {
          margin-right: 0; }
        &:hover {
          opacity: 0.7; } } } }
  &__register-q1, &__register-q2 {
    position: absolute;
    color: $color-white;
    font-size: rem(13);
    font-family: $font-roboto-bold;
    bottom: 7px;
    font-style: normal;
    text-align: center;
    text-transform: uppercase;
    padding-top: 46px;
    b {
      font-size: rem(18); }
    span {
      display: block;
      line-height: 1.2; } }
  &__register-q1 {
    left: -130px; }
  &__register-q2 {
    right: -130px; } }

.mfp-bg-register {
  background-image: url('../img/popup-register-background.png');
  background-position: center center;
  background-repeat: no-repeat; }
